<template>
  <div>
    <v-card-title class="text-h5" style="color: #273746"> </v-card-title>
    <v-card elevation="0" outlined>
      <v-divider class="mx-4"> </v-divider>
      <v-card-text>
        <div>
          <br />
          <v-expansion-panels accordion v-model="panel">
            <v-expansion-panel v-for="(item, i) in itemsT" :key="i">
              <v-expansion-panel-header
                >{{ item.number }}) {{ item.specificPoint }}
              </v-expansion-panel-header>
              <v-expansion-panel-content>
                <div>
                  <v-card elevation="0" outlined>
                    <v-card-text>
                      <div class="text-right">
                        <v-btn icon @click="openDeleteModal(item)">
                          <v-icon>delete</v-icon>
                        </v-btn>
                      </div>
                      <!--  Aqui empiezan los datos buenos.. -->
                      <v-text-field
                        v-model="item.specificPoint"
                        outlined
                        label="Punto específico"
                        required
                        @change="sentChangeItems"
                      ></v-text-field>

                      <v-select
                        outlined
                        v-model="item.type"
                        :items="typeItems"
                        label="Tipo"
                        item-text="name"
                        required
                        @change="item.observation = automaticValue(item)"
                        return-object
                      >
                      </v-select>

                      <v-text-field
                        outlined
                        v-model="item.measuredValue"
                        label="Valor medido"
                        type="number"
                        required
                        @input="item.observation = automaticValue(item)"
                      ></v-text-field>

                      <v-text-field
                        outlined
                        v-model="item.observation"
                        value="3"
                        label="Observación"
                        required
                        disabled
                        @change="sentChangeItems"
                      ></v-text-field>

                      <v-textarea
                        label="Notas"
                        auto-grow
                        outlined
                        name="input-7-4"
                        v-model="item.notes"
                        @change="sentChangeItems"
                      ></v-textarea>
                    </v-card-text>
                  </v-card>
                </div>
              </v-expansion-panel-content>
            </v-expansion-panel>
          </v-expansion-panels>
        </div>
      </v-card-text>
      <div class="text-center">
        <v-btn rounded color="primary" dark @click="addPoint()">
          Nuevo punto específico
        </v-btn>
      </div>
      <br />
    </v-card>

    <v-dialog v-model="dialog" persistent max-width="290">
      <v-card>
        <v-card-title class="text-h5"> Borrar </v-card-title>
        <v-card-text>
          ¿Seguro que desea borrar el punto específico?, Al borrar se ajustará
          el contador.</v-card-text
        >
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="#273746" text @click="dialog = false"> Cancelar </v-btn>
          <v-btn outlined color="#273746" @click="deleteItem(idItemDelete)">
            Si, Borrar
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
  name: "ItemLand",
  props: {
    template: Object,
  },
  data() {
    return {
      idItemDelete: null,
      dialog: false,
      itemsT: [],
      typeItems: [
        {
          id: 1,
          name: "Pararrayo",
          maxValue: 10,
        },
        {
          id: 2,
          name: "Equipo de utilización",
          maxValue: 25,
        },
      ],
      panel: null,
    };
  },
  watch: {
    value(e) {
      this.$emit("setOption", e);
    },
    overallPhoto(e) {
      this.$emit("setOverallPhoto", e);
    },
    itemsT: {
      deep: true,
      handler() {
        this.$emit("setItemsT", this.itemsT);
      },
    },
    template() {
      this.start();
    },
  },
  created() {
    this.start();
  },
  methods: {
    start() {
      if (this.template.itemsT) {
        this.itemsT = this.template.itemsT;
      }
    },
    addPoint() {
      const end = this.itemsT.length + 1;
      this.panel = this.itemsT.length;
      const newPoint = {
        number: end,
        specificPoint: "",
        type: null,
        measuredValue: "",
        observation: "",
        notes: "",
      };
      this.itemsT.push(newPoint);
    },
    show() {
      console.log(this.itemsT);
      this.$emit("setItemsT", this.itemsT);
    },
    sentChangeItems() {
      console.log("change");
    },
    openDeleteModal(item) {
      this.idItemDelete = item;
      this.dialog = true;
    },
    deleteItem(getItem) {
      let temp = this.itemsT.filter((item) => item.number != getItem.number);
      console.log(temp);
      if (temp.length > 0) {
        let count = 0;
        let accommodate = temp.map((item) => {
          count++;
          return {
            number: count,
            specificPoint: item.specificPoint,
            type: item.type,
            measuredValue: item.measuredValue,
            observation: item.observation,
            notes: item.notes,
          };
        });
        this.itemsT = accommodate;
        console.log("accommodate");
        console.log(accommodate);
      }
      if (temp.length == 0) {
        this.itemsT = [];
      }
      this.panel = null;
      this.dialog = false;
    },
    automaticValue(e) {
      if (e.type == null) return "";
      if (e.measuredValue == "") return "";

      if (!isNaN(e.measuredValue)) {
        return e.measuredValue > e.type.maxValue ? "NO CUMPLE" : "CUMPLE";
      }
      return "";
    },
  },
};
</script>

<style>
.v-chip.v-size--default {
  border-radius: 20px;
  font-size: 14px;
  height: 42px;
}
</style>